import { useRef, useEffect } from "react";
import LearnMoreBtn from "./LearnMoreBtn";
import { Link } from "react-router-dom";

import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

const ThirdDiv = () => {
  let thirdDivRef = useRef(null);

  useEffect(() => {
    gsap.to(thirdDivRef.current, {
      opacity: 1,
      scale: 0.9,
      duration: 1,
      scrollTrigger: {
        trigger: thirdDivRef.current,
        start: "top",
        end: "center",
        toggleActions: "play reverse play reverse",
      },
    });
  });

  return (
    <div className="third_div" ref={thirdDivRef}>
      <div className='thirdDiv_left'>
        <h2>Kepler-22b</h2>
        <Link to="/Kepler22b">
          <LearnMoreBtn topStyle="65%" leftStyle="50%" />
        </Link>
      </div>
      <div className="thirdDiv_right">

      </div>
    </div>
  );
};

export default ThirdDiv;
