import { useRef, useEffect } from "react";
import LearnMoreBtn from "./LearnMoreBtn";
import { Link } from "react-router-dom";

import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

const FifthDiv = () => {
  let fifthDivRef = useRef(null);

  useEffect(() => {
    gsap.to(fifthDivRef.current, {
      opacity: 1,
      scale: 0.9,
      duration: 1,
      scrollTrigger: {
        trigger: fifthDivRef.current,
        start: "top",
        end: "center",
        toggleActions: "play reverse play reverse",
      },
    });
  });

  return (
    <div className="fifth_div" ref={fifthDivRef}>
      <div>
        <h2>Booking</h2>
        <Link to="/ReservationPage">
          <LearnMoreBtn topStyle="65%" leftStyle="50%" />
        </Link>
      </div>
    </div>
  );
};

export default FifthDiv;
